<template>
  <el-container>
    <el-header>
      <el-menu
        :default-active="activeIndex2"
        class="el-menu-demo header-nav"
        mode="horizontal"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="text-align:right"
      >
        <el-submenu index="2">
          <template slot="title">{{ nickname }}</template>
          <el-menu-item index="2-2" @click="infoVisible = true">我的信息</el-menu-item>
          <el-menu-item index="2-3" @click="dialogFormVisible = true">修改密码</el-menu-item>
          <el-menu-item index="2-1" @click="out">退出</el-menu-item>
        </el-submenu>
       

        <el-menu-item index="3" disabled>企业管理中心</el-menu-item>
            <el-menu-item index="3">
              
                消息 <span style="color:#f00; font-weight:bold">(3)</span>
            </el-menu-item>
      </el-menu>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          background-color="#545c64"
          :router="true"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-menu-item index="/welcome">
            <i class="el-icon-s-home"></i>
            <span slot="title">首页</span>
          </el-menu-item>
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-s-promotion"></i>
              <span>任务管理</span>
            </template>
            <el-menu-item-group>
              <template slot="title">任务</template>
              <el-menu-item index="/admin/task/list" router>任务列表</el-menu-item>
              <el-menu-item index="/admin/task/contract" router>合同管理</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="6">
            <template slot="title">
              <i class="el-icon-user-solid"></i>
              <span>人才管理</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/enterprise/bind_user">人才列表</el-menu-item>
              <el-menu-item index="2-2">申请列表</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-s-order"></i>
              <span>订单管理</span>
            </template>
            <el-menu-item-group>
              <template slot="title">分组一</template>
              <el-menu-item index="2-1">选项1</el-menu-item>
              <el-menu-item index="2-2">选项2</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title">
              <i class="el-icon-s-cooperation"></i>
              <span>账务管理</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/admin/enterprise/wallet">账户余额</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="4">
            <template slot="title">
              <i class="el-icon-s-tools"></i>
              <span>系统设置</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/admin/enterprise/invoice">发票信息</el-menu-item>
              <el-menu-item index="4-1">申请发票</el-menu-item>
              <el-menu-item index="/admin/enterprise/address">邮寄信息</el-menu-item>
              <el-menu-item index="4-1">发票查询</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group>
              <template slot="title">企业信息</template>
              <el-menu-item index="/admin/enterprise/certification">认证信息</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view link="/welcome"></router-view>
      </el-main>
      <el-dialog
        title="企业信息"
        :visible.sync="infoVisible"
        width="500px"
        :center="true"
        :close-on-click-modal="false"
      >
        
        我的消息

      </el-dialog>
      <el-dialog
        title="修改密码"
        :visible.sync="dialogFormVisible"
        width="500px"
        :center="true"
        :close-on-click-modal="false"
      >
        <el-form :model="form" ref="form" :rules="rules" :status-icon="true">
          <el-form-item label="旧密码：" :label-width="formLabelWidth" prop="pre_password">
            <el-col :span="18">
              <el-input v-model="form.pre_password" autocomplete="off"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="新密码：" :label-width="formLabelWidth" prop="new_password">
            <el-col :span="18">
              <el-input v-model="form.new_password" show-password autocomplete="off"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="新密码："  :label-width="formLabelWidth" prop="sign_pwd">
            <el-col :span="18">
              <el-input v-model="form.sign_pwd" show-password autocomplete="off"></el-input>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="editPwd('form')">确 定</el-button>
        </div>
      </el-dialog>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      formLabelWidth: "80px",
      dialogFormVisible: false,
      infoVisible:false,
      nickname :"",
      form: {
        pre_password: "",
        new_password: "",
        sign_pwd: ""
      },
      rules: {
        pre_password: [
          { required: true, message: "旧密码必须输入", trigger: "blur" }
        ],
        new_password: [
          { required: true, message: "新密码必须输入", trigger: "blur" }
        ],
        sign_pwd: [
          { required: true, message: "新密码必须输入", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.nickname = localStorage.getItem('nickname');
    let token = localStorage.getItem("token");
    if (!token) {
      this.$router.push("/admin/login");
    }
  },
  methods: {
    editPwd(formName) {
      let self = this;
      this.$refs[formName].validate(valid => {
        if (valid) {

          if( this.form.new_password != this.form.sign_pwd)
          {
            this.$message.error('两次新密码不一致');
            return;
          }
          this.$api.post('/enterprise/edit_pass', this.form, res=>{
              if( res.data.bol )
              {
                this.$message.success(res.data.msg);
                setTimeout(function() {
                  self.dialogFormVisible = false;
                }, 1500);
              }else{
                this.$message.error(res.data.msg)
              }
          });
          
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    out() {
      let self = this;
      this.$message.warning("退出成功");
      localStorage.removeItem("token");
      setTimeout(function() {
        self.$router.push("/admin/login");
      }, 2000);
    }
  }
};
</script>

<style>
body {
  padding: 0;
  margin: 0;
}
.el-header {
  padding: 0;
  margin: 0;
}
.header-nav li {
  float: right !important;
}
html,
.gr__localhost,
.el-container,
body,
.el-container,
.el-menu {
  height: 100%;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
}
.mt15 {
  margin-top: 15px;
}
</style>



